/* MQ 消息队列 */
export default {
  namespaced: true,
  state: {
    MQ: {},
  },
  getters: {
    //订阅消息
    subscribe(state) {
      return state.MQ;
    }
  },

  mutations: {
    // 发布MQ主题
    publish(state, options) {
      state.MQ = {timestamp:new Date().getTime(),message:options.message,topic:options.topic}
      //state.MQ.set(options.topic,{timestamp:new Date().getTime(),data:options.data});
      //state.MQ = {timestamp:new Date().getTime(),toppic:options.toppic,data:options.data};
      /* let script = `
    state.MQ.`+ options.toppic + ` = {timestamp:` + new Date().getTime() + `,data:'` + options.data + `'}
   `
      eval(script); */
      //state.MQ.set(options.toppic,);
      //console.log(state);
     // console.log(script);
    },
  }
}



